.container {
    position: relative;
    width: 100%;
    height: 100%;
}

.scanner-box {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.scanner-overlay {
    position: absolute;
    top: -50px;
    /* 让扫描框从外部开始 */
    left: 0;
    width: 100%;
    height: 60px;
    /* 扫描框的高度 */
    border: 1px solid rgba(0, 255, 0, 0.6);
    background: rgba(0, 255, 0, 0.3);
    animation: scanAnimation 2s infinite;
    /* 无限循环扫描动画 */
}

@keyframes scanAnimation {
    0% {
        top: -50px;
        /* 扫描框起始位置 */
    }

    50% {
        top: 50%;
        /* 扫描框在中间 */
        transform: translateY(-50%);
        /* 精确居中 */
    }

    100% {
        top: 100%;
        /* 扫描框到达底部 */
    }
}